rol', 'General Settings') }}\r\n\t\t\t\t\t\t</a>\r\n\t\t\t\t\t</li>\r\n\t\t\t\t</ul>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n\tname: 'Navigation',\r\n\tdata() {\r\n\t\treturn {\r\n\t\t\tcurrentTab: 'dashboard',\r\n\t\t\tsettingsOpen: false,\r\n\t\t\tmenuItems: [\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'dashboard',\r\n\t\t\t\t\tlabel: 'Dashboard',\r\n\t\t\t\t\ticon: 'icon-home',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'clients',\r\n\t\t\t\t\tlabel: 'Clients',\r\n\t\t\t\t\ticon: 'icon-contacts',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'domains',\r\n\t\t\t\t\tlabel: 'Domains',\r\n\t\t\t\t\ticon: 'icon-public',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'hostings',\r\n\t\t\t\t\tlabel: 'Hosting',\r\n\t\t\t\t\ticon: 'icon-category-office',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'websites',\r\n\t\t\t\t\tlabel: 'Websites',\r\n\t\t\t\t\ticon: 'icon-link',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'services',\r\n\t\t\t\t\tlabel: 'Services',\r\n\t\t\t\t\ticon: 'icon-settings',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'invoices',\r\n\t\t\t\t\tlabel: 'Invoices',\r\n\t\t\t\t\ticon: 'icon-files',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'projects',\r\n\t\t\t\t\tlabel: 'Projects',\r\n\t\t\t\t\ticon: 'icon-folder',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'tasks',\r\n\t\t\t\t\tlabel: 'Tasks',\r\n\t\t\t\t\ticon: 'icon-checkmark',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'transactions',\r\n\t\t\t\t\tlabel: 'Income/Expense',\r\n\t\t\t\t\ticon: 'icon-category-office',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'debts',\r\n\t\t\t\t\tlabel: 'Debts/Receivables',\r\n\t\t\t\t\ticon: 'icon-files',\r\n\t\t\t\t},\r\n\t\t\t\t{\r\n\t\t\t\t\tid: 'reports',\r\n\t\t\t\t\tlabel: 'Reports',\r\n\t\t\t\t\ticon: 'icon-category-monitoring',\r\n\t\t\t\t},\r\n\t\t\t],\r\n\t\t}\r\n\t},\r\n\tmounted() {\r\n\t\t// Listen for tab changes from external code\r\n\t\tif (typeof window.DomainControl !== 'undefined') {\r\n\t\t\t// Store original switchTab method\r\n\t\t\tconst originalSwitchTab = window.DomainControl.switchTab\r\n\t\t\t// Override to update Vue component\r\n\t\t\twindow.DomainControl.switchTab = (tabName) => {\r\n\t\t\t\tthis.currentTab = tabName\r\n\t\t\t\tif (originalSwitchTab) {\r\n\t\t\t\t\toriginalSwitchTab.call(window.DomainControl, tabName)\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t}\r\n\r\n\t\t// Initialize current tab from URL or default\r\n\t\tconst urlParams = new URLSearchParams(window.location.search)\r\n\t\tconst tabFromUrl = urlParams.get('tab')\r\n\t\tif (tabFromUrl) {\r\n\t\t\tthis.currentTab = tabFromUrl\r\n\t\t}\r\n\t},\r\n\tmethods: {\r\n\t\ttranslate(appId, text, vars) {\r\n\t\t\t// Use Nextcloud's translation system\r\n\t\t\ttry {\r\n\t\t\t\tif (typeof window !== 'undefined') {\r\n\t\t\t\t\tif (typeof OC !== 'undefined' && OC.L10n && typeof OC.L10n.translate === 'function') {\r\n\t\t\t\t\t\tconst translated = OC.L10n.translate(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t\tif (typeof window.t === 'function') {\r\n\t\t\t\t\t\tconst translated = window.t(appId, text, vars || {})\r\n\t\t\t\t\t\tif (translated && translated !== text) {\r\n\t\t\t\t\t\t\treturn translated\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t}\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.warn('Translation error:', e)\r\n\t\t\t}\r\n\r\n\t\t\t// Fallback: Manual translation\r\n\t\t\tconst translations = {\r\n\t\t\t\t'Dashboard': 'Dashboard',\r\n\t\t\t\t'Clients': 'Mteriler',\r\n\t\t\t\t'Domains': 'Domainler',\r\n\t\t\t\t'Hosting': 'Hosting',\r\n\t\t\t\t'Websites': 'Websiteler',\r\n\t\t\t\t'Services': 'Hizmetler',\r\n\t\t\t\t'Invoices': 'Faturalar',\r\n\t\t\t\t'Projects': 'Projeler',\r\n\t\t\t\t'Tasks': 'Grevler',\r\n\t\t\t\t'Income/Expense': 'Gelir/Gider',\r\n\t\t\t\t'Debts/Receivables': 'Bor/Alacak',\r\n\t\t\t\t'Reports': 'Raporlar',\r\n\t\t\t\t'Settings': 'Ayarlar',\r\n\t\t\t\t'General Settings': 'Genel Ayarlar',\r\n\t\t\t}\r\n\r\n\t\t\treturn translations[text] || text\r\n\t\t},\r\n\t\thandleTabClick(tabId) {\r\n\t\t\tthis.currentTab = tabId\r\n\t\t\t// Call existing DomainControl.switchTab if available\r\n\t\t\tif (typeof window.DomainControl !== 'undefined' && window.DomainControl.switchTab) {\r\n\t\t\t\twindow.DomainControl.switchTab(tabId)\r\n\t\t\t}\r\n\t\t},\r\n\t\ttoggleSettings() {\r\n\t\t\tthis.settingsOpen = !this.settingsOpen\r\n\t\t},\r\n\t},\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n#app-navigation {\r\n\twidth: 300px;\r\n\tflex-shrink: 0;\r\n\tbackground-color: var(--color-main-background);\r\n\tborder-right: 1px solid var(--color-border);\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\theight: 100vh;\r\n}\r\n\r\n#app-navigation ul {\r\n\tpadding: 10px;\r\n\tflex: 1;\r\n\toverflow-y: auto;\r\n\tlist-style: none;\r\n\tmargin: 0;\r\n}\r\n\r\n#app-navigation ul li {\r\n\tposition: relative;\r\n\tmargin-bottom: 2px;\r\n}\r\n\r\n#app-navigation ul li a {\r\n\tdisplay: block;\r\n\theight: 44px;\r\n\tline-height: 44px;\r\n\tpadding: 0 12px 0 44px;\r\n\tborder-radius: var(--border-radius-large);\r\n\tcolor: var(--color-main-text);\r\n\tbackground-position: 14px center;\r\n\tbackground-repeat: no-repeat;\r\n\tbackground-size: 20px;\r\n\ttransition: background-color 0.2s, color 0.2s;\r\n\twhite-space: nowrap;\r\n\toverflow: hidden;\r\n\ttext-overflow: ellipsis;\r\n\ttext-decoration: none;\r\n\tcursor: pointer;\r\n}\r\n\r\n#app-navigation ul li a:hover,\r\n#app-navigation ul li a:focus {\r\n\tbackground-color: var(--color-background-hover);\r\n}\r\n\r\n#app-navigation ul li.active a {\r\n\tbackground-color: var(--color-primary-element);\r\n\tcolor: var(--color-primary-element-text);\r\n}\r\n\r\n#app-navigation ul li.active a [class^='icon-'],\r\n#app-navigation ul li.active a [class*=' icon-'] {\r\n\tfilter: brightness(0) invert(1);\r\n}\r\n\r\n.app-navigation-entry {\r\n\tborder-top: 1px solid var(--color-border);\r\n\tpadding: 10px;\r\n}\r\n\r\n.set